import { HUtils } from "@macashipo/mlib"
import { Component } from "react"
import styled from "styled-components"

class MUIProjectItem extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {}
  render() {
    const data = this.props.data || {}
    return (
      <WrapLayout>
        <div
          className="a-title"
          onClick={() => {
            HUtils.runFuntion(this.props.fnList, "onClickItem", [data])
          }}
        >
          {data.Name}
        </div>
      </WrapLayout>
    )
  }
}
const WrapLayout = styled.div`
  padding: 20px;
  background: white;
  margin: 20px;
  border-radius: 4px;
  border: 1px solid #d5d5d5;
  min-height: 100px;
  cursor: pointer;
  & .a-title {
    font-weight: bold;
  }
`
export default MUIProjectItem
